.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.7rem 2rem;
    width: 100%;
    box-shadow: 10px 5px 20px 5px var(--primary-color);
    border: 0px transparent;
    position: fixed;
    top: 0;
    z-index: 100;
    background-color: #fff;
    opacity: 0.9;
  }
  
  .nav-brand {
    display: flex;
    align-items: center;
  }
  
  .logo {
    height: 50px;
    width: 50px;
    margin-right: 0.5rem;
  }
  
  .navbar ul {
    display: flex;
  }
  
  .navbar a {
    color: var(--dark-color);
    padding: 0.75rem 1.5rem;
    margin: 0 0.25rem;
    border-radius: 10px;
    transition: background-color 0.4s ease-in, color 0.4s ease-in;
  }
  
  .navbar a:hover {
    background-color: var(--primary-color);
    color: #fff;
  }
#landing {
    padding: 10rem 0;
    height: 100vh;
  }
  
  .showcase-content {
    text-align: center;
  }
  
  .showcase-content p {
    font-weight: bold;
  }
  
  .showcase-buttons {
    margin: auto;
    width: 300px;
  }
  
  #landing .btn {
    margin: 0 0.5rem;
  }
  
  #landing span {
   /* text-decoration: underline var(--secondary-color) 5px;*/
  }
  
#stats {
    position: relative;
    background-color: var(--light-color);
    padding: 2rem 0;
  }
  
  .stats-bg {
    position: absolute;
    width: 170px;
    height: 200px;
    background: radial-gradient(#61dfc3 20%, transparent 20%);
    background-size: 20px 20px;
    left: 100px;
    top: 30px;
  }
  
  .stats-cards {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
  }
  
  .stats-card {
    background-color: #fff;
    padding: 1rem 2rem;
    border-radius: 20px;
    text-align: center;
    box-shadow: 10px 5px 20px 0 rgba(229, 149, 0, 0.7);
    z-index: 10;
  }
  
  .stats-card i {
    color: var(--primary-color);
  }
  
  
/* Services */
.service {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2rem 0;
    font-family: Poppins,sans-serif;
  }
  
  .service-text {
    width: 40%;
  }
  
  .service-text h2 {
    font-size: 2.25rem;
  }
  
  .service-text p {
    color: var(--dark-color);
    font-size: 1rem;
    max-width: 450px;
  }
  
  .service-img {
    height: 280px;
    width: 35%;
    border-radius: 20px;
  }
  
  .service-1 {
    background: url(/Incubation/static/media/service1.30b2763b.svg) no-repeat center center/cover;
  }
  
  .service-2 {
    background: url(/Incubation/static/media/service2.837956d9.svg) no-repeat center center/cover;
  }
  
  .service-3 {
    background: url(/Incubation/static/media/service3.9249aa48.svg) no-repeat center center/cover;
  }
  
  .service-4 {
    background: url(/Incubation/static/media/service4.d390f1b2.svg) no-repeat center center/cover;
  }
#register {
    height: 6rem;
  }
  
  #register h2 {
    display: inline-block;
    font-size: 3rem;
    margin-top: 1rem;
  }
  
  #register a {
    float: right;
    margin-top: 2rem;
  }
  
.reg-card {
    position: absolute;
    top: 15vh;
    left: 20vw;
    height: 80vh;
    width: 60vw;
    background-color: var(--light-color);
    border: 2px dotted var(--primary-color);
    border-radius: 10px;
    padding: 1.5rem;
  }
  
  .reg-header h2 {
    display: inline-block;
  }
  
  .reg-header button {
    float: right;
    border-radius: 5px;
    margin-top: 0.5rem;
  }
  
  .input-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .reg-body input {
    padding: 0.3rem 0.6rem;
  }
  
  .reg-body button {
    border-radius: 10px;
    float: right;
    margin-top: 0.5rem;
  }
