:root {
  --primary-color: #e59500;
  --secondary-color: #1B1A17;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utilities */
.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #e59500;
  color: var(--primary-color);
}

.text-secondary {
  color: #1B1A17;
  color: var(--secondary-color);
}

.text-dark {
  color: #343a40;
  color: var(--dark-color);
}

/* Padding */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* Margin */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

.btn {
  display: inline-block;
  background: #f4f4f4;
  background: var(--light-color);
  background-color: #e59500;
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 50px;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.btn-primary,
.bg-primary {
  background: #e59500;
  background: var(--primary-color);
  color: #fff;
}

.btn-light,
.bg-light {
  background: #f4f4f4;
  background: var(--light-color);
  color: #e59500;
  color: var(--primary-color);
  border: 1px #e59500 solid;
  border: 1px var(--primary-color) solid;
}

.btn-dark,
.bg-dark {
  background: #343a40;
  background: var(--dark-color);
  color: #fff;
}

.btn-danger,
.bg-danger {
  background: #dc3545;
  background: var(--danger-color);
  color: #fff;
}

.btn-success,
.bg-success {
  background: #28a745;
  background: var(--success-color);
  color: #fff;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn:hover {
  opacity: 0.8;
}

.round-img {
  border-radius: 50%;
}

.line {
  height: 1px;
  background: #343a40;
  background: var(--dark-color);
  width: 100%;
  margin: 0.5rem 0;
}

/* Overlay */
.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

